html, body { margin:0; padding:0; overflow:hidden }
.svg{
  /* border-style:dashed; */
  background-color:rgb(15, 21, 80);
  position:relative; top:0; bottom:0; left:0; right:0;
  border: solid 2px white;
  border-radius: .25rem;
}

.morocco{
  cursor: pointer;
  fill: rgb(110, 156, 156);
}
.morocco:hover{
  stroke-width: 1.5;
  stroke: rgb(128, 56, 21);
}